ClB,2CAA2C;IAC3C,2CAA2C;IAC3C,2CAA2C;AAC/C;AACA;IACI,cAAc;AAClB;;;AAGA,kCAAkC;;AAElC;IACI,cAAc;IACd,cAAc;IACd,cAAc;IACd,kBAAkB;IAClB,2CAA2C;IAC3C,2CAA2C;IAC3C,2CAA2C;AAC/C;;;AAGA,gCAAgC;;AAEhC;IACI,oBAAoB;AACxB;;AAEA;;IAEI;QACI,aAAa;IACjB;;IAEA;QACI,0BAA0B;QAC1B,gGAAgG;IACpG;;IAEA;QACI,iBAAiB;QACjB,kDAAkD;QAClD,oCAAoC;QACpC,4CAA4C;IAChD;AACJ","sourcesContent":["/* all other stylesheets have been incorporated into this one (except editor-cell-element) */\n\n/* See settings/theme-settings.js for definition of --theme-* properties */\n\n:root {\n    /* focus transition */\n\n    --focus-x1-delay:                       0s;\n    --focus-x1-duration:                    0.2s;\n    --focus-x1-easing:                      cubic-bezier(0.42, 0.0, 0.0, 1.0);\n\n    --focus-x2-delay:                       calc(var(--focus-x1-duration) / 4);\n    --focus-x2-duration:                    0.1s;\n    --focus-x2-easing:                      cubic-bezier(0.42, 0.0, 0.0, 1.0);\n}\n\n\nbody {\n    overflow: hidden;  /* inhibit scrolling for body */\n    margin: 0 0 0 1px;  /* prevent left border from getting absorbed into browser window frame */\n    background-color: var(--by-bgc);\n}\n\nmain {\n    /* enable scrolling for main element */\n    overflow: auto;\n    max-height: calc(100vh - var(--theme-hd-h));\n    max-width:  100vw;\n}\n\nheader {\n    display: flex;\n    align-items: flex-end;\n    justify-content: flex-start;\n\n    height: var(--theme-hd-h);\n    background-color: var(--theme-hd-bgc);\n    border-bottom: var(--theme-hd-bdw) var(--theme-hd-bds) var(--theme-hd-bdc);\n    border-radius: var(--theme-hd-bdr);\n}\n\nheader > tool-bar {  /* next to menu */\n    align-self: center;\n    margin-left: auto;  /* push to far right */\n\n    visibility: visible;\n    max-height: 100vh;\n    padding: 0 0.5em;\n    border: 0;\n    background-color: transparent;\n}\n\n.eval-cell-output {\n    display: block;\n    white-space: pre-wrap;  /* display white-space properly */\n    line-height: 140%;\n\n    padding: var(--theme-ou-p);\n    background-color: var(--theme-ou-bgc);\n\n    /* no highlight */\n    border-width: 0 0 0 var(--theme-ou-hlw);\n    border-color: transparent;\n    border-style: var(--theme-ou-hls);\n}\n.eval-cell-output:has(+ tool-bar),\n.eval-cell-output:has(+ eval-cell) {\n    padding-bottom: var(--theme-cl-p-inter);\n}\n\n\n/* output element active highlight */\neval-cell[data-active] + .eval-cell-output {\n    border-width: 0 0 0 var(--theme-ou-hlw);\n    border-color: transparent transparent transparent var(--theme-ou-hlc);\n    border-style: var(--theme-ou-hls);\n}\n\n\n/* === visible/active - animated attributes === */\n\n/* bare tool-bar and eval-cell base definitions: they define a collapsed state */\n\ntool-bar {\n    overflow: hidden;\n\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n    gap: var(--theme-tl-g);\n\n    background-color: var(--theme-tl-bgc);\n\n    /* prevent tabbing to contained controls by setting visibility:hidden */\n    /* this is necessary because we don't set display:none because we want to animate the transition */\n    visibility: hidden;\n\n    max-height:   0;\n    padding:      var(--theme-tl-p);\n    border-width: 0;\n    /* split border-color into the three specific sides; the bottom side is controlled separately by the input type selector */\n    border-top-color:   var(--theme-tl-bdc);\n    border-right-color: var(--theme-tl-bdc);\n    border-left-color:  var(--theme-tl-bdc);\n\n    transition: max-height   var(--focus-x1-duration) var(--focus-x1-easing) var(--focus-x1-delay);\n    transition: padding      var(--focus-x2-duration) var(--focus-x2-easing) var(--focus-x2-delay);\n    transition: border-width var(--focus-x2-duration) var(--focus-x2-easing) var(--focus-x2-delay);\n    transition: border-color var(--focus-x2-duration) var(--focus-x2-easing) var(--focus-x2-delay);\n\n    border-radius: var(--theme-cl-bdr) var(--theme-cl-bdr) 0 0;\n    border-style:  var(--theme-tl-bds);\n\n}\n\neval-cell {\n    overflow: hidden;\n\n    display: block;\n    margin:  0;\n\n    outline: 0px solid transparent;  /* disable default contenteditable outline */\n\n    white-space: pre-wrap;  /* properly display white-space */\n    font-family: var(--theme-cl-ff);\n    line-height: var(--theme-cl-lh);\n\n    background-color: var(--theme-cl-bgc);\n\n    max-height:   0;\n    padding:      var(--theme-cl-p);\n    border-width: 0;\n    border-color: transparent var(--theme-cl-bdc) var(--theme-cl-bdc) var(--theme-cl-bdc);\n\n    transition: max-height   var(--focus-x1-duration) var(--focus-x1-easing) var(--focus-x1-delay);\n    transition: padding      var(--focus-x2-duration) var(--focus-x2-easing) var(--focus-x2-delay);\n    transition: border-width var(--focus-x2-duration) var(--focus-x2-easing) var(--focus-x2-delay);\n    transition: border-color var(--focus-x2-duration) var(--focus-x2-easing) var(--focus-x2-delay);\n\n    border-radius: 0 0 var(--theme-cl-bdr) var(--theme-cl-bdr);\n    border-style:  var(--theme-cl-bds);\n}\n\n/* tool-bar and eval-cell visible but not active */\ntool-bar:has(+ eval-cell[data-visible]),\ntool-bar:has(+ eval-cell[contenteditable]) {\n    visibility:   visible;\n    max-height:   100vh;\n    border-width: var(--theme-tl-bdw);\n    border-radius: var(--theme-tl-bdr);\n}\neval-cell[data-visible],\neval-cell[contenteditable] {\n    max-height:   100vh;\n    border-width: var(--theme-cl-bdw);\n}\n\n/* tool-bar and eval-cell visible and active */\ntool-bar:has(+ eval-cell[data-visible][data-active]),\ntool-bar:has(+ eval-cell[contenteditable][data-active]) {\n    border-top-color:   var(--theme-tl-bdc-active);\n    border-right-color: var(--theme-tl-bdc-active);\n    border-left-color:  var(--theme-tl-bdc-active);\n}\neval-cell[data-visible][data-active],\neval-cell[contenteditable][data-active] {\n    border-color: transparent var(--theme-cl-bdc-active) var(--theme-cl-bdc-active) var(--theme-cl-bdc-active);\n}\n\n\n/* color selected by eval-cell input-type */\n\ntool-bar option,\ntool-bar select {\n    background-color: var(--theme-ty-bgc-markdown);\n}\n\ntool-bar option[value=\"javascript\"],\ntool-bar > select:has(> option[value=\"javascript\"]:checked) {\n    background-color: var(--theme-ty-bgc-javascript);\n}\n\ntool-bar option[value=\"tex\"],\ntool-bar > select:has(> option[value=\"tex\"]:checked) {\n    background-color: var(--theme-ty-bgc-tex);\n}\n\n\ntool-bar:has(option) {\n    border-bottom-color: var(--theme-ty-bgc-markdown);\n}\n\ntool-bar:has(option[value=\"javascript\"]:checked) {\n    border-bottom-color: var(--theme-ty-bgc-javascript);\n}\n\ntool-bar:has(option[value=\"tex\"]:checked) {\n    border-bottom-color: var(--theme-ty-bgc-tex);\n}\n\n\n/* === tool-bar indicators and controls === */\n\ntool-bar > * {\n    margin: 0;  /* pack controls within tool-bar; let flex gap handle spacing */\n    background-color: var(--theme-tl-bgc);  /* same \"default\" color as parent tool-bar */\n}\n\n.tool-bar-indicator {\n    display: block;\n    width:  0.5em;\n    height: 0.5em;\n    margin: 0;\n    padding: 0;\n    border-radius: 50%;\n    border: 1px solid var(--theme-ti-bdc);\n    background-color: var(--theme-ti-bgc);\n}\n\n.tool-bar-indicator.running[data-indicator-value=\"on\"] {\n    border: 1px solid var(--theme-ti-bdc-running);\n    background-color: var(--theme-ti-bgc-running);\n}\n.tool-bar-indicator.modified[data-indicator-value=\"on\"] {\n    border: 1px solid var(--theme-ti-bdc-modified);\n    background-color: var(--theme-ti-bgc-modified);\n}\n\ntool-bar > select > option {\n    background-color: var(--theme-ty-bgc);\n}\n\n\n/* === toggle-switch === */\n\ntoggle-switch {\n    appearance: none;\n    border: 0;\n    width:  var(--theme-ts-w);\n    height: var(--theme-ts-h);\n}\n\ntoggle-switch svg {\n    width:  auto;\n    height: 100%;\n}\n\ntoggle-switch.tool-bar-toggle-editable                      svg .accent-fill   { fill:   var(--theme-ts-bgc-